import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Game')
export class Game extends Component {
    @property({type:Node})
        spriteWood: Node

    @property({type:Node})
        spriteKnife: Node

    static canThrow: boolean = false;
    static spriteWoodRotation: number = null;
    static defaultSpriteKnife = null;
    static arrayKnife: Node[] = [];

    onLoad() {
        Game.canThrow = true;
        Game.spriteWoodRotation = 2.5;
        Game.arrayKnife = [];
        Game.defaultSpriteKnife = this.spriteKnife.position;

        this.node.on('touchstart', this.knifeThrow, this);
    }

    onDestroy() {
        this.node.off('touchstart', this.knifeThrow, this);
    }

    start() {

    }

    update(deltaTime: number) {
        
    }

    knifeThrow() {
        console.log('Click!!');
    }
}


